body {
	margin: 0;
	background: #141d26;
	color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', 
    monospace;
}

/* This is for controlling the particles and background */
canvas{
  display:block;
  vertical-align:bottom;
  opacity: 0.4;
}

/* Animations for classes */
@keyframes headamp {
	0% {
		opacity: 0%;
		transform: translateY(-20%);
	}
	100% {
		opacity: 100%;
		transform: translateY(0%);
	}
}

@keyframes fadeamp {
	0% {
		opacity: 0%;
	}
	100% {
		opacity: 100%;
	}
}

.initialpage h1 {
	position: relative;
	text-align: center;
	font-family: 'arial';
	font-size: 40px;
	animation: 0.5s ease-in headamp;
}

.initialpage p {
	position: relative;
	text-align: center;
	font-family: 'arial';
	font-size: 20px;
	animation: 0.5s ease-in headamp;
}

.dataentry {
	position: relative;
	text-align: center;
}
 
.textbox-1 {
    display: inline-block;
    border: 2px solid red;
    height: 195px;
    width: 444px;
    padding-top: 15px;
    border-radius: 5px;
    animation: 0.5s ease-in fadeamp;
}

.midgap {
	display: inline-block;
	margin-left: 20px;
	margin-right: 20px;
}

.textbox-1 p {
	font-size: 20px;
	text-align: center;
	padding: 25px;
	animation: 0.5s ease-in fadeamp;
}

.textbox-2 {
	display: inline-block;
    border: 2px solid green;
    height: 195px;
    width: 444px;
    padding-top: 15px;
    border-radius: 5px;
    animation: 0.5s ease-in fadeamp;
}

.textbox-2 p {
	font-size: 20px;
	text-align: center;
	padding: 25px;
	animation: 0.5s ease-in fadeamp;
}

/* Impedance textbox */
#first {
	border-radius: 5px;
	background: white;
}

/* Sensitivity textbox */
#second {
	border-radius: 5px;
	background: white;
}

/* Placeholder of textboxes */
input {
	padding: 5px 1px 5px;
	font-size: 15px;
	text-align: center;
	background-color: lightgrey;
}

input#first[type=text]:focus {
	border-color: red;
	background-color: darkgrey;
}

input#second[type=text]:focus {
	border-color: green;
	background-color: darkgrey;
}

.submit {
	display: block;
}

#submit {
	display: block;
	text-align: center;
	margin-left: 35%;
	margin-right: 35%;
	margin: auto;
	font-size: 20px;
	border-radius: 10px;
	border-color: orange;
	background-color: #ff9933;
	padding: 10px 20px;
	animation: 0.5s ease-in fadeamp;
}

#submit:hover {
	background-color: #ff8000;
}

#powerentry {
	padding: 5px;
	margin-left: 10px;
	border-radius: 5px;

}

#powerentry:hover {
	background-color: lightgrey;
}

#sources {
	position: relative;
	font-size: 12px;
	bottom: 0px;
	margin-left: auto;
	margin-right: auto;
}

/* This is for the whole Tableresults */
.tableresults {
	position: relative;
	display: block;
	text-align: center;
	font-family: 'arial';
	animation: 0.5s ease-in fadeamp;
	padding-bottom: 30px;
	width: 50%;
	margin-left: 25%;
	margin-right: 25%;
}

/* Makes the border of the table merge better */
.tableouter {
	position: relative;
	border-collapse: collapse;
	text-align: center;
	font-size: 18px;
}

/* For the table headers */
.tablouter tr, th {
	border: solid;
	padding: 15px 10px;
}

/* For the table body */
.tableouter tr, td {
	border: 1px solid;
	padding: 10px;
	text-overflow: auto;
	text-align: center;
}